import React, { useState } from "react";
import ReactDom from "react-dom/client";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCartShopping,
  faStar,
  faLocationDot,
} from "@fortawesome/free-solid-svg-icons";
import resObj from "../data";
const Header = ({ResList,setFilterList}) => {
  const [Value, setValue] = useState("search");
  
 
  
  const handleSearch = () => {
    {
      if(!Value){
        console.log({Value})

        setFilterList([])
      }
      else{
       console.log(ResList);

        setFilterList(ResList.filter((obj) => {
          const myData = obj?.info;
          const res=Value.toLowerCase()
          setValue("");
          return myData.name.toLowerCase().includes(res);
        }));
        // setResList(FilterList);
      }
    
    }
  };

  return (
    <div className="header">
      <div className="logo-cont">
        <img
          className="logo"
          src="https://thumbs.dreamstime.com/b/food-delivery-logo-design-template-134749604.jpg"
          alt="image not found"
        />
      </div>
      <div className="search-tab">
        <input
          type="text"
          value={Value}
        
          onChange={(e) => {
            setValue(e.target.value);
          }}
        />
        <button onClick={() => handleSearch()}>Search</button>
      </div>
      <div className="nav-items">
        <ul>
          <li className="Li">Home</li>
          <li className="Li">About Us</li>
          <li className="Li">Contact Us</li>
          <li>
            <FontAwesomeIcon icon={faCartShopping} />
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Header;
