import React, { useEffect, useState } from "react";
import ReactDom from "react-dom/client";
import Header from "./components/Header";
import Body from "./components/Body";
import resObj from "./data";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCartShopping,
  faStar,
  faLocationDot,
} from "@fortawesome/free-solid-svg-icons";
import "./App.css";
import Shimmer from "./components/Shimmer";
import { menuApi,Api } from "./utills";

const App = () => {
  const [ResList, setResList] = useState([]);
  const [FilterList, setFilterList] = useState([]);

  useEffect(() => {
    findRes();
  }, []);

  async function findRes() {
    const res = await fetch(Api)
    const req = await res.json();

    const resList =
      req?.data?.cards[4]?.card?.card?.gridElements?.infoWithStyle?.restaurants;

    setResList(resList);
    setFilterList(resList);
  }

  return (
    <>
      <Header setFilterList={setFilterList} ResList={ResList} />
      {(ResList.length === 0) ? <Shimmer /> : <Body ResList={FilterList} />}
    </>
  );
};
export default App;

